/* ------------------------------navbar.js------------------------------------ */

.navheader
  {
    background-color: black;
    padding-left: 20px;
  }
.nav{
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
}
.brand h3:hover{
  color: white;
  transition: 0.01 ease-in;
  cursor: pointer;
}
.pages{
  padding-top: 6px;
  color: white;
  padding-left: 20px;
}
.pages a:hover{
  font-weight: 500;
  transition: 0.2s ease-in;
  cursor: pointer;
  text-decoration: none;
  color: #7d7f7f;
}
.pages a{
  padding-left: 20px;
  color: white;
}

.pg:focus{
  color: #7d7f7f;
}