/* -------------------------Heading.js(banner background)------------------------------------------ */

.bannerImage{
  background-image: url('./assets/img4.jpg');
  background-attachment: fixed;
  background-size: cover;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.banner{
  text-align: -webkit-center;
  vertical-align: middle;
  padding: 100px;
  background-color: #68666310;
  opacity: 0.8;
  height: 300px;
}
.banner h3{
  margin: auto;
  color: white;
}

/* ------------------------------about.js----------------------------------------- */

.imga img {
  max-width: 100%;
  border-radius: 15px;
}
.abou {
  padding-top: 80px;
  padding-bottom: 80px;
}

/* --------------------------------service.js---------------------------------------- */

.serve{
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #68666310;
}
.scard{
  text-align: center;
}
.card{
  border: none;
  margin: 20px 0;
}
.icon{
  width: 60px;
  padding: 10px;
}

/* ------------------------------------portfolio.js---------------------------------------------- */

.pwork {
  text-align: center;
}
.wp {
  overflow: hidden;
  padding: 12px;
}
.portf {
  padding-top: 100px;
  padding-bottom: 80px;
}
.wp img {
  width: 100%;
}
.wp img:hover {
  filter: blur(3px);
}

.middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}
.wp:hover .middle {
  opacity: 1;
}

.text {
  color: white;
  font-size: 16px;
  padding: 16px 32px;
}

/* ----------------------------------we.js(video)----------------------------------------- */
.we-are {
  padding-top: 80px!important;
}
.watch {
  background-image: url('./assets/work0.jpg');
  background-size: cover;
  border-radius: 15px;
  box-shadow: 0px 7px 20px 5px #9d8760;
  display: flex;
  min-height: 300px;
}
.watch a img{
  width: 50px;
  margin: auto;
}
.watch a{
  margin: auto;
}

/* -------------------------------------blog.js-------------------------------------------------- */

.blog{
  padding-top: 100px;
  padding-bottom: 100px;
}
.bloghead{
  text-align: center;
}
.blogcard {
  background-color: #68666310;
}

/* -----------------------------------------footer.js---------------------------------------------------- */

.footer{
  padding-bottom: 100px;
  padding-top: 100px;
  background-color: rgb(193, 138, 42);
  box-shadow: 0px -2px 20px 1px #b1822f;
}
.foot ul li{
  list-style: none;
}
.socialicon a{
  padding:10px;
}
.socialicon a img:hover{
  width: 21px;
}
.ft li a{
  color: black;
  text-decoration: none;
}
.ft li a:hover{
  font-weight: 700;
  transition: 0.2s ease-in;
  cursor: pointer;
}

.socialicon a img {
  width: 20px;
}

/* -------------------------------------------team.js---------------------------------------------------------- */

.team{
  padding-top: 100px;
  padding-bottom: 100px;
}
.topteam{
  text-align: center;
}
.teamgcard {
  background-color: #68666310;
}

/* ---------------------------------------------contactpage.js------------------------------------------------------- */

.contact{
  justify-content: space-between;
}
.form{
  padding-top: 30px;
  padding-bottom: 60px;
}

.address{
  padding-top: 70px;
  padding-bottom: 60px;
}